<template>
    <div page-index>
        <div v-shadow="{ radius: 8 }">
            <p style="font-family: 'Signature'">naihe</p>
            <p>
                <router-link
                    to="/route/child/16666666666"
                    class="custom-link"
                    >路由传参</router-link
                >
                <router-link
                    to="/route/child/16666666667"
                    class="custom-link"
                    >路由传参</router-link
                >
            </p>
        </div>
        <div
            style="
                position: absolute;
                top: 26px;
                left: 40%;
                width: -webkit-fill-available;
            ">
            <router-view></router-view>
        </div>
        <NHCard title="关于路由">
            <pre>
npm install vue-router

路由传参:
  {
    path: '/route/:tel',
    // 该配置项一定要开启, 否则组件中props属性接收不到数据
    props: true
  }
  在需要接收数据的组件中使用props属性接收

两个新的钩子函数 beforeRouteEnter和beforeRouteLeave
拥有路由守卫所支持的参数
beforeRouteEnter: 当前路由进入时调用
beforeRouteLeave: 当前路由离开时调用
以上两个函数都需要调用next()函数
            </pre>
        </NHCard>
        <p class="title-bar">临时扩展</p>
        <p>
            <img
                class="nh-pic"
                :src="imgUrl"
                width="280"
                height="160" />
        </p>
        <p class="drag-file"></p>
    </div>
</template>

<script>
let dragBox;
let prevImg;
/**
 * 图片选取预览
 * @param {*} files
 */
function handleFiles(files) {
    for (let i = 0; i < files.length; i++) {
        let file = files[i];
        let imageType = /^image\//;

        if (!imageType.test(file.type)) {
            continue;
        }

        let img = document.createElement('img');

        img.style['width'] = '280px';
        img.style['height'] = '160px';
        img.style['position'] = 'absolute';
        img.style['top'] = '0';
        img.style['left'] = '0';
        img.file = file;
        if (
            !prevImg ||
            (prevImg.name !== file.name &&
                prevImg.size !== file.size &&
                prevImg.type !== file.type)
        ) {
            prevImg = file;
            dragBox.appendChild(img);
        }

        let reader = new FileReader();
        reader.onload = (function (aImg) {
            return function (e) {
                aImg.src = e.target.result;
            };
        })(img);
        reader.readAsDataURL(file);
    }
}

// import imgUrl from 'C:/Users/naihe/Pictures/uToolsWallpapers/wallpaper1674025342416.jpg';
const imgUrl = require('C:/Users/naihe/Pictures/uToolsWallpapers/wallpaper1674025342416.jpg');

export default {
    name: 'Route',
    data() {
        return {
            imgUrl: imgUrl,
        };
    },
    mounted() {
        dragBox = document.querySelector('.drag-file');
        dragBox.addEventListener(
            'dragenter',
            function dragenter(e) {
                e.stopPropagation();
                e.preventDefault();
            },
            false
        );
        dragBox.addEventListener(
            'dragover',
            function dragover(e) {
                e.stopPropagation();
                e.preventDefault();
            },
            false
        );
        dragBox.addEventListener(
            'drop',
            function drop(e) {
                e.stopPropagation();
                e.preventDefault();

                let dt = e.dataTransfer;
                let files = dt.files;

                handleFiles(files);
            },
            false
        );
    },
    beforeRouteEnter(to, from, next) {
        next()
    },
    beforeRouteLeave(to, from, next) {
        next()
    },
};
</script>

<style scoped>
.drag-file {
    position: relative;
    width: 280px;
    height: 160px;
    border: 1px dashed;
    text-align: center;
}

.drag-file::after {
    content: '拖拽图片进行上传';
    position: absolute;
    right: -1.1rem;
    width: 16px;
    border: 1px dashed #f6416c;
    border-left-style: none;
}

.title-bar {
    position: relative;
    background: linear-gradient(135deg, #fbed96, #abecd6);
    color: #422256;
    width: 400px;
    height: 2rem;
    line-height: 2rem;
    padding-left: 10px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    letter-spacing: 0.5rem;
}

.title-bar::before {
    position: absolute;
    left: 0;
    content: '';
    width: 8px;
    height: inherit;
    background: linear-gradient(135deg, #fff6b7, #f6416c);
}
</style>
